.wishes-section {
	position: relative;
	.wishes-form-wrapper {
		position: relative;
		padding: 8rem 0;
		@include max-md {
			padding: 5rem 0;
		}
	}
	.card-form {
		background-color: transparent;
		border-radius: var(--border-radius);
		text-align: right;
		&.card {
			border: none;
		}
		.card-body {
			padding: 2rem;
			@include max-md {
				padding: 2rem 1rem;
			}
		}
	}

	.wishes-preview {
		position: relative;
		padding: 3rem 0 8rem;
		@include max-md {
			padding: 2rem 0 5rem;
		}
		.card {
			position: relative;
			border: none;
			background-color: transparent;
			.card-body {
				overflow: hidden;
				padding: 2rem 0;
				.wishes {
					text-align: left;
					max-height: 32rem;
					overflow: auto;
					padding-right: 1rem;
					color: var(--color-dark);
					&::-webkit-scrollbar {
						width: 5px;
					}
					&::-webkit-scrollbar-track {
						background-color: #eee;
						border-radius: 20px;
						opacity: 0.15;
					}
					&::-webkit-scrollbar-thumb {
						background-color: var(--color-secondary);
						border-radius: 20px;
					}
					// .text-muted {
					// 	color: rgba($color: #fff, $alpha: 0.8) !important;
					// }
					.wish {
						margin-bottom: 1.5rem;
						display: grid;
						grid-template-columns: auto 1fr;
						gap: 0.5rem;
						h6 {
							font-weight: bold;
						}
						h6,
						p {
							margin-bottom: 0;
						}
						.wish-badge {
							background-color: transparent;
							width: 35px;
							aspect-ratio: 1;
							display: flex;
							justify-content: center;
							align-items: center;
							margin-top: 0.5rem;
							border: 1px solid var(--color-button-trans);
							border-radius: 50px;
						}
						.wish-description {
							padding: 1rem;
							background-color: transparent;
							border-radius: 5px;
							position: relative;
							border: 1px solid var(--color-button-trans);
							border-radius: var(--border-radius);
							// &::before {
							// 	position: absolute;
							// 	top: 15px;
							// 	left: 0;
							// 	width: 15px;
							// 	height: 15px;
							// 	transform: translateX(-100%);
							// 	clip-path: url(#wishes-polygon);
							// 	content: "";
							// 	background-color: var(--color-button-trans);
							// }
							// &::after {
							// 	position: absolute;
							// 	top: 15.5px;
							// 	left: 0;
							// 	width: 14px;
							// 	height: 13px;
							// 	transform: translateX(-100%);
							// 	clip-path: url(#wishes-polygon);
							// 	content: "";
							// 	background-color: var(--color-main);
							// }
						}
					}
				}
			}
		}
	}
}
